  //       setSizes({width: 'auto', height: 'auto'});
  //       setShow(false);
  //       return
  //     }

  //     const {naturalHeight: pH, naturalWidth: pW} = pictureRef.current;
  //     const {offsetHeight: wH, offsetWidth: wW} = wrapperRef.current;
  //     if(pH === 0 && pW === 0) {
  //       setRestart(!restart)
  //       return
  //     }
