export default function(ev) {
  debugger
  let superData = ev; //  ,  , 
  let boost = 0;
  let maxPath = '';
  let grid = []; //  

  function promiseLessSeries(arrOfFunc) { //   (   ,    )
    let i = 0;
    while(i < arrOfFunc.length) {
      arrOfFunc[i]();
      i++;
    }
  };
  async function checker(isModif, isBoost, data = superData) { //      
    //      )
    
    let branch = data.branch; 
    let choses = branch.choseCount; //    .
    let sery = []; //   .
    let i = 0;

    isModif && findAndSet({path: data.pos, height: branch.boost, data});

    if(choses !== 0) { //   ,    (   )
      //    
      while(i < choses) {
        sery.push(branch['q'+i])
        i++
      }
      let funcArr = sery.map((branch, i) => {
        return function() {
          checker(isModif, isBoost, branch)
        }
      });
      promiseLessSeries(funcArr) //    

      isBoost && (() => { //     (    ) 
        if(data.pos.split('').every(el => el==="0")) { // check if this bottom
          branch.boost = 0;
        } else { //     ,     --->    
          branch.boost = branch['q'+~~((choses%2===0 ? (choses-1) : choses)/2)].branch.boost;
        };
      })()


    } else { //    ,    ,  
    
      isBoost && (() => { // 
        branch['boost'] = boost; //  
        boost++;
        if(maxPath.length < data.pos.length) { //     
          maxPath = data.pos
        }
      })()
      //console.log(this)
    };
    
  }

  return new Promise((resolve, reject) => {
    (function updater() {
      checker(false, true).then(() => {
        //       this.boost ()  this.maxPath ()
        grid = Array(boost).fill('').map(() => []).map(() => Array(maxPath.length).fill('').map(() => ({data: null})));
  
        checker(true, false).then(() => {
          grid = grid.reverse();
  
          resolve({data: {grid}});
        })
      })
        
    })()
  })

  function findAndSet({path, height, data}) {
    grid[+height][path.length-1].data = data;
  }
}